@import "vars";
@import "libs";
@import "fonts";

::placeholder {
	color: #666; }
html, body {
	min-height: 100vh; }
html {
	font-size: 10px; }
body {
	font-size: 1.7rem;
	min-width: 320px;
	position: relative;
	line-height: 1.47;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	input, textarea {
		display: block;
		width: 100%;
		outline: none;
		resize: none;
		border: none; }
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		padding: 0; } }
b, strong {
	font-weight: bold; }
p {
	margin-bottom: 1.5rem; }
a {
	color: $accent;
	text-decoration: underline;
	transition: 0.25s;
	&[href^="#"] {
		border-bottom: .2rem dotted $accent;
		text-decoration: none;
		&:hover {
			border-color: transparent; } }
	&:hover {
		color: inherit;
		text-decoration: none; }
	&.link {
		&-author {
			border-bottom: .1rem dotted $accent;
			text-decoration: none;
			&:hover {
				border-color: transparent; } } } }
.w-100 {
	width: 100%; }
.main {
	.container-fluid {
		padding: {
			left: 0;
			right: 0; } } }
.list {
	&-unstyled {
		@include list-unstyled; } }
.icon {
	display: inline-block;
	vertical-align: middle;
	top: -0.3rem;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	&-left {
		margin-right: 1rem;
		&_small {
			margin-right: 0.5rem; } }
	&-right {
		margin-left: 1rem; }
	&-hamburger {
		width: 2rem;
		height: 1.9rem; }
	&-home {
		width: 1.5rem;
		height: 1.6rem; }
	&-search {
		@include size(1.4rem); }
	&-plus {
		@include size(4.5rem);
		background-image: url(../img/icon/icon-plus.png); }
	&-author {
		@include size(1.6rem); }
	&-dropdown {
		@include size(1rem); }
	&-add {
		@include size(3rem); }
	&-camera {
		@include size(2rem, 1.8rem); }
	&-star {
		@include size(1.6rem, 1.5rem); }
	&-stars {
		@include size(2rem); }
	&-eye {
		@include size(2.2rem, 1.5rem);
		&_small {
			@include size(2rem); } }
	&-heart {
		@include size(2.2rem, 2rem); }
	&-review {
		@include size(3.2rem); }
	&-access {
		@include size(3rem, 3.1rem); }
	&-adv {
		@include size(3rem); }
	&-company {
		@include size(3rem, 3.1rem); }
	&-up {
		@include size(1.7rem, 1.4rem); }
	&-basket {
		@include size(3rem); }
	&-edit {
		@include size(3rem);
		stroke: #91B1D9; }
	&-aright {
		@include size(.8rem, 1.2rem); }
	&-metro, &-location, &-phone {
		@include size(1.5rem); }
	&-close {
		@include size(.7rem, .8rem);
		background-image: url(../img/icon/icon-close.png); }
	&-closeb {
		@include size(1.2rem, 1.3rem); }
	&-closel {
		@include size(1.8rem, 1.9rem); }
	&-down {
		@include size(2.3rem, 2.4rem); }
	&-done {
		@include size(6rem); } }
.image {
	display: block;
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	&-con {
		background-size: contain; }
	&-cov {
		background-size: cover; }
	&-round {
		border-radius: 0.9rem; } }
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	margin-bottom: 0; }
h1, .h1 {
	font-size: 6rem;
	font-weight: 700;
	line-height: 1; }
h2, .h2 {
	font-size: 4.5rem;
	font-weight: 700;
	line-height: 1.33; }
h3, .h3 {
	font-size: 2.7rem;
	font-weight: 500;
	line-height: 1.3; }
h4, .h4 {
	font-size: 2.1rem;
	font-weight: 700;
	line-height: 1.43; }
h5, .h5 {
	font-size: 1.7rem;
	font-weight: 700;
	line-height: 1.47; }
h6, .h6 {
	font-size: 1.5rem;
	font-weight: 700;
	line-height: 1.47; }
.text {
	&-medium {
		font-size: 1.7rem;
		line-height: 1.47; }
	&-small {
		font-size: 1.5rem;
		line-height: 1.47; }
	&-xsmall {
		font-size: 1.3rem; }
	&-xxsmall {
		font-size: 1.2rem; }
	&-success {
		color: #37b452; }
	&-primary {
		color: $accent; }
	&-secondary {
		color: #7989b5;
		* {
			color: #7989b5!important; } }
	&-danger {
		color: #ff2d53; }
	&-info {
		color: #90b1d9; }
	&-vk {
		color: #133fc5; }
	&-facebook {
		color: #1f55f8; }
	&-insta {
		color: #bb00b4; }
	&-ok {
		color: #ff7a00; } }
.btn {
	display: flex;
	cursor: pointer;
	transition: 0.25s;
	justify-content: center;
	align-items: center;
	font-family: $lato-font;
	text-decoration: none!important;
	&-xsmall {
		height: 2.4rem;
		border: 0.2rem;
		font-size: 1.3rem;
		padding: {
			left: 1rem;
			right: 1rem; } }
	&-small {
		height: 3rem;
		border-radius: 0.5rem;
		font-size: 1.3rem;
		min-width: 11rem;
		padding: {
			left: 2rem;
			right: 2rem; }
		.icon-left {
			margin-left: -1rem; } }
	&-medium {
		height: 3.5rem;
		border-radius: 0.5rem;
		font-size: 1.3rem;
		min-width: 10rem;
		padding: {
			left: 2.5rem;
			right: 2.5rem; } }
	&-big {
		height: 4rem;
		border-radius: 0.5rem;
		font-size: 1.3rem;
		padding: {
			left: 2.3rem;
			right: 2.3rem; }
		.icon {
			margin-left: -1.1rem; } }
	&-large {
		height: 4.5rem;
		border-radius: 0.5rem;
		font-size: 1.5rem;
		padding: {
			left: 2.5rem;
			right: 2.5rem; }
		.icon:not(.icon-right) {
			margin-left: -1.1rem; } }
	&-primary {
		background-color: $accent;
		color: #ffffff;
		font-weight: 700;
		&:hover {
			text-shadow: 0 0.1rem 0.1rem rgba(0, 0, 0, 0.25);
			background-color: #0369e8;
			color: #ffffff; }
		&:active {
			color: #d3d3d3;
			background-color: #055fd0; } }
	&-secondary {
		color: $accent;
		background-color: #c7d7eb;
		&:hover {
			background-color: #abb8ca; }
		&:active {
			color: #3e74b7;
			background-color: #abb8ca; } }
	&-light {
		color: $accent;
		background-color: #ffffff;
		border: 0.1rem solid #c7d7eb;
		&:hover {
			box-shadow: 0 0.3rem 0.6rem rgba(132, 157, 188, 0.3);
			background-color: #ffffff;
			color: $accent; }
		&:active {
			color: #496e9b;
			background-color: #d5dbe3; } }
	&-dark {
		box-shadow: 0 0.2rem 0.6rem rgba(0, 0, 0, 0.2), 0 0 0.2rem rgba(0, 0, 0, 0.1);
		color: #ffffff;
		background-color: #253751;
		font-weight: 700;
		&:hover {
			background-color: #334a6a;
			box-shadow: 0 0.3rem 0.6rem #849dbc;
			color: #ffffff; }
		&:active {
			color: #cbcbcb;
			background-color: #202f44;
			box-shadow: none; } }
	&-danger {
		color: #ffffff;
		background-color: #ff2d53;
		font-weight: 700;
		&:hover {
			text-shadow: 0 0.1rem 0.1rem rgba(0, 0, 0, 0.25);
			background-color: #e6294b; }
		&:active {
			color: #f491a4;
			background-color: #dc294a; } }
	&[disabled] {
		color: #8a8a8a!important;
		background-color: rgba(200, 200, 200, 0.45)!important;
		font-weight: 700; }
	&-icon {
		padding: {
			left: 0;
			right: 0; }
		min-width: auto;
		.icon {
			margin-left: 0; }
		&.btn {
			&-small {
				width: 3rem; }
			&-medium {
				width: 3.5rem; }
			&-big {
				width: 4rem; }
			&-large {
				width: 4.5rem; } } }
	&-round {
		border-radius: 50%; }
	&-link {
		display: block; } }
.badge {
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	&-box {
		margin: {
			right: -.4rem;
			left: -.4rem;
			bottom: -0.4rem; }
		> * {
			padding: {
				left: 0.4rem;
				right: 0.4rem; }
			margin-bottom: 0.4rem; } }
	&-icon {
		&.badge {
			&-medium {
				width: 2.5rem;
				min-width: auto; }
			&-small {
				width: 2rem;
				min-width: auto; }
			&-large {
				width: 5rem;
				min-width: auto; } } }
	&-medium {
		height: 2.5rem;
		font-size: 1.3rem;
		font-weight: 700;
		border-radius: 0.3rem;
		min-width: 4rem;
		padding: {
			left: 1rem;
			right: 1rem; } }
	&-small {
		height: 2rem;
		font-weight: 500;
		letter-spacing: 0.08rem;
		text-transform: uppercase;
		border-radius: 0.4rem;
		font-size: 1rem;
		min-width: 4rem;
		padding: {
			left: 1rem;
			right: 1rem; } }
	&-rating {
		height: 2.1rem;
		font-weight: 700;
		font-size: 1.3rem;
		border-radius: 0.3rem;
		min-width: 3.5rem;
		padding: {
			left: .8rem;
			right: .8rem; } }
	&-large {
		height: 5rem; }
	&-round {
		border-radius: 50%; }
	&-accent {
		background-color: $accent; }
	&-primary {
		background-color: #74d1ee; }
	&-secondary {
		background-color: #ffffff;
		box-shadow: 0 0.1rem 0.1rem rgba(0, 0, 0, 0.15);
		color: #7989b5;
		&.badge-hover {
			&:hover {
				background-color: #D5DBE3; } } }
	&-success {
		background-color: #37b452; }
	&-danger {
		background-color: #ff2d53; }
	&-warning {
		background-color: #ffba42; }
	&-info {
		background-color: #90b1d9;
		color: #ffffff;
		&.badge-hover {
			&:hover {
				background-color: darken(#90b1d9,5%); } } }
	&-light {}
	&-dark {}
	&-hover {
		cursor: pointer; } }
.table {
	width: 100%;
	margin: {
		top: 2.5rem;
		bottom: 4.5rem; }
	tr {
		border-bottom: .1rem solid #cccccc;
		td, th {
			padding: 1.4rem 1.5rem;
			&:first-child {
				padding-left: 0; }
			&:last-child {
				padding-right: 0; } } } }
.hidden {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	pointer-events: none; }
.block, .card {
	&-link {
		display: block;
		width: 100%;
		text-decoration: none;
		color: inherit; } }
.form {
	display: block;
	&-title {
		border-bottom: .1rem solid #c7d7eb;
		padding-bottom: 1.3rem;
		margin-bottom: 4.4rem; }
	&-group {
		margin-bottom: 2.5rem; }
	&-address {
		margin: {
			left: -1.5rem;
			right: -1.5rem; }
		> * {
			padding: {
				left: 1.5rem;
				right: 1.5rem; } }
		.form {
			&-label {
				padding-top: 0; }
			&-control {
				width: 5.8rem; } } }
	&-phone {
		> * {
			&:first-child {
				padding-right: .2rem; }
			&:nth-child(2) {
				padding: {
					left: 0.2rem;
					right: 0.75rem; } }
			&:last-child {
				padding: {
					left: 0.75rem; } } }
		.form-control {
			width: 22.4rem; } }
	&-time {
		margin: {
			left: -0.5rem;
			right: -0.5rem; }
		> * {
			padding: {
				left: 0.5rem;
				right: 0.5rem; } }
		.form-control {
			width: 8rem;
			text-align: center; }
		.form-text {
			padding-top: 0; } }
	&-text {
		display: block;
		font-size: 1.4rem;
		padding-top: 0.8rem; }
	&-submit {
		margin-top: 3rem;
		.btn {} }
	&-label {
		display: block;
		font-size: 1.4rem;
		margin-bottom: 0;
		padding-top: .8rem;
		color: #253751;
		+ * {
			margin-top: 0.5rem; }
		.form-label_info {
			margin-top: 0.7rem;
			padding-top: 0; }
		&_info {
			color: #c7d7eb;
			font-size: 1.3rem;
			display: block;
			padding-top: 0.9rem; } }
	&-control {
		height: 4rem;
		display: flex;
		align-items: center;
		width: 100%;
		font-size: 1.5rem;
		padding: {
			left: 1.4rem;
			right: 1.4rem; }
		border-radius: 0.3rem;
		border: 0.1rem solid #c7d7eb;
		background-color: #ffffff;
		transition: 0.25s;
		&::placeholder {
			color: #c7d7eb; }
		&:focus {
			border-color: #90b1d9; } }
	textarea.form-control {
		height: 14.5rem;
		align-items: flex-start;
		padding: {
			top: .8rem;
			bottom: .8rem; } }
	&-checkbox, &-radio {
		+ .form-label {
			cursor: pointer;
			position: relative;
			padding-top: 0;
			&:before, &:after {
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				transition: 0.25s;
				left: 0; } } }
	&-checkbox {
		+ .form-label {
			padding-left: 3rem;
			&:before {
				@include size(2rem);
				border-radius: 0.2rem;
				border: 0.1rem solid #c7d7eb;
				background-color: #ffffff; } }
		&:checked + .form-label:before {
			background: $accent url(../img/icon/icon-check.svg) center no-repeat;
			border-color: $accent; } }
	&-days {
		box-shadow: 0 0 0.2rem rgba(13, 35, 67, 0.1), 0 0.3rem 0.8rem rgba(13, 35, 67, 0.1);
		width: 35.6rem;
		> * {
			border-left: .1rem solid #c7d7eb;
			&:first-child {
				border-left: none; } }
		.form-checkbox:checked + .form-label {
			color: $accent;
			&:after {
				visibility: visible;
				opacity: 1; } }
		.form-label {
			@include size(5rem, 3.2rem);
			text-align: center;
			color: #90b1d9;
			font-size: 1.3rem;
			font-weight: 500;
			position: relative;
			padding-top: 0;
			padding-left: 0;
			line-height: 3.2rem;
			position: relative;
			&:before {
				display: none; }
			&:after {
				content: '';
				display: block;
				@include size(100%, .2rem);
				background-color: $accent;
				transition: 0.25s;
				left: 0;
				bottom: 0;
				opacity: 0;
				visibility: hidden;
				position: absolute;
				transform: translateY(0);
				top: auto; } } }
	&-gallery {
		margin-top: 2rem;
		.form-label {
			@include size(10rem);
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: rgba(#c7d7eb, 0.5);
			cursor: pointer;
			padding-top: 0; }
		#map + .form-label {
			border-radius: 100%; } }
	&-search {
		position: relative;
		.form-control {
			border-radius: 0.6rem;
			background-color: #d6e1ef;
			border: none;
			padding: {
				left: 1.7rem;
				right: 4rem; }
			color: #7989b5;
			&::placeholder {
				color: #7989b5; } } }
	&-geo {
		padding-left: 3.5rem; }
	&-registration {
		.btn-medium {
			height: 4rem; }
		.form-label {
			padding-top: 0; }
		.form-group {
			margin-bottom: 1.7rem; }
		.form-submit {
			.btn {
				width: 100%; } } }
	&-message {
		display: block;
		margin-top: 3rem;
		@extend .text-small, .text-secondary;
		em {
			font-style: normal;
			color: #253751; } }
	&-rating {
		height: 4rem;
		.badge-medium {
			height: 3rem;
			min-width: 4.8rem; }
		&_item {
			display: block;
			cursor: pointer;
			@include size(2rem);
			background-image: url(../img/icon/icon-star.svg);
			background-size: contain;
			background-position: center;
			background-repeat: no-repeat;
			&:hover:before, &.is-active:before {
				opacity: 1;
				visibility: visible; }
			&:before {
				content: '';
				display: block;
				background-size: contain;
				background-position: center;
				background-repeat: no-repeat;
				@include size(100%);
				opacity: 0;
				visibility: hidden;
				background-image: url(../img/icon/icon-star_fill.svg);
				transition: 0.25s; } } } }
.card {
	background-color: #ffffff;
	transition: 0.25s;
	padding: 1rem 1.5rem;
	height: 100%;
	position: relative;
	&-box {
		margin-bottom: -1rem;
		> * {
			margin-bottom: 1rem; } }
	&-image {
		padding-bottom: 70%;
		margin: -.4rem -.9rem 0; }
	&-shadow {
		&_small {
			box-shadow: 0 0.5rem 1rem rgba(102, 116, 166, 0.4); }
		&_big {
			box-shadow: 0 1.2rem 2.4rem rgba(102, 116, 166, 0.15); } }
	&-round {
		border-radius: 0.9rem; }
	&-link {
		color: #253751;
		&:hover {
			box-shadow: 0 0 0.2rem rgba(13, 35, 67, 0.1), 0 0.3rem 0.8rem rgba(13, 35, 67, 0.3);
			color: #253751;
			.card-title {
				color: $accent; } }
		&:active {
			box-shadow: 0 0 0.2rem rgba(13, 35, 67, 0.1), 0 0.3rem 0.6rem rgba(13, 35, 67, 0.05);
			background-color: #f2f2f2; } } }
.index {
	.header-bar {
		padding: {
			left: 3rem; }
		flex: 0 0 31.5rem;
		max-width: 31.5rem; } }
.is-index {
	.footer {
		padding-left: 31.5rem; } }
.login {
	.header {
		position: relative;
		z-index: 10;
		padding-right: 37rem; }
	.header-bar {
		background: none;
		flex: 0 0 25.6rem;
		max-width: 25.6rem; }
	.container-fluid {
		padding: {
			left: 1rem;
			right: 1rem; } } }
.result-v.is-results {
	.footer {
		padding-left: 63.6rem; } }
.public, .result {
	.header {
		position: relative;
		z-index: 10;
		&-bar {
			flex: 0 0 49rem;
			max-width: 49rem; } } }
// header
.header {
	box-shadow: 0 0.8rem 1.6rem rgba(102, 116, 166, 0.15);
	background-color: #f8f9fe;
	&-bar {
		background-color: #ffffff;
		padding-right: 3rem;
		.row {
			height: 7rem; } }
	&-navbar {
		padding: {
			left: 4rem;
			right: 3rem; } }
	&-hamburger {
		box-shadow: inset 0 0 0.3rem rgba(0, 0, 0, 0.15);
		border-radius: 0.5rem;
		background-color: #f2f7fa;
		padding: .5rem!important; }
	&-logo {
		img {
			max-width: 100%; } }
	&-city {
		font-size: 1.2rem;
		display: flex;
		> * {
			display: block; }
		&_text {
			font-family: $lato-font;
			font-weight: 700;
			margin-right: 1rem; }
		&_name {
			overflow: hidden;
			display: block;
			text-overflow: ellipsis;
			white-space: nowrap;
			max-width: 100%;
			display: inline-block;
			border-bottom: 0.1rem dashed #0172ff;
			text-decoration: none; }
		&_select {
			text-decoration: none;
			position: relative;
			text-transform: uppercase;
			cursor: pointer;
			color: $accent;
			width: 8rem;
			.icon {
				position: absolute;
				top: 50%;
				right: 0;
				transform: translate(1rem, -50%);
