// owl-carousel
.owl {
	&-nav {
		position: absolute;
		top: 50%;
		left: 50%;
		width: calc(100% + 2rem);
		display: flex;
		justify-content: space-between;
		transform: translate(-50%, -50%);
		pointer-events: none; }
	&-prev, &-next {
		transition: 0.25s;
		pointer-events: auto;
		.icon {
			stroke: #fff; }
		&.disabled {
			opacity: 0;
			visibility: hidden; } }
	&-prev {
		.icon {
			transform: rotate(-180deg); } } }
