@import "vars";


/*==========  Desktop First  ==========*/
// ≥1500px
@include media-breakpoint-down(xxxl) {
	.result {
		.footer-nav {
			padding: {
				left: 1rem;
				right: 1rem; }
			&_list {
				margin: {
					left: -1rem;
					right: -1rem; }
				> * {
					padding: {
						left: 1rem;
						right: 1rem; } } } } } }
// ≥1500px
@include media-breakpoint-down(xxl) {
	.header-navbar {
		padding: {
			left: 1rem;
			right: 1rem; } }
	.header-user,
	.header-nav,
	.footer-nav {
		padding: {
			left: 1rem;
			right: 1rem; }
		&_list {
			margin: {
				left: -1rem;
				right: -1rem; }
			> * {
				padding: {
					left: 1rem;
					right: 1rem; } } } }
	.result {
		.results-content {
			height: calc(100vh - 17rem); }
		.footer {
			padding-left: 0; } }
	.result-v {
		.results-content {
			height: calc(100vh - 12rem); }
		.footer {
			padding-left: 0!important; } } }
// ≥1300px
@include media-breakpoint-down(xl) {
	.form-geo {
		padding-left: 0; }
	.footer-geo_text:before {
		margin-right: 0.5rem; }
	.footer-geo {}
	// 	position: absolute
	// 	right: 100%
	// 	z-index: 10
	// 	white-space: nowrap
	.map {
		height: calc(100vh - 7rem); }
	.result, .public {
		.header-bar {
			padding-right: 3rem;
			flex: 0 0 31.5rem;
			max-width: 31.5rem;
			padding-left: 3rem; }
		.map {
			height: calc(100vh - 12rem); }
		&.result-v {
			.map {
				height: calc(100vh - 7rem); } } }
	.profile {
		padding: {
			left: 1rem;
			right: 1rem; }
		max-width: 30rem;
		flex: 0 0 30rem;
		&-pages + .col-auto {
			flex-basis: calc(100% - 55.6rem);
			max-width: calc(100% - 55.6rem); } }
	.login {
		.header {
			padding-right: 30rem; } } }
// ≥1200px
@include media-breakpoint-down(lg) {
	.map-add {
		right: 1.5rem;
		bottom: 1.5rem; }
	.login {
		.header {
			padding-right: 0; } }
	.adv-title {
		background: none;
		height: auto;
		width: auto;
		padding-left: 0;
		margin-left: 0; }
	.public .main {
		.container-fluid {
			padding-left: 1rem;
			padding-right: 1rem; } }
	.public {
		.breadcrumbs-list {
			margin: {
				left: -1rem;
				right: -1rem; }
			&_item {
				padding: {
					left: 1rem;
					right: 1rem; } } } }
	.result {
		.results-content {
			height: calc(100vh - 12rem); } }
	.result-v {
		.results-content {
			height: calc(100vh - 7rem); } }
	.tooltip {
		display: none!important; }
	.profile {
		&-pages + .col-auto {
			flex-basis: calc(100% - 25.6rem);
			max-width: calc(100% - 25.6rem); } }
	.login {
		.header-bar {
			flex-basis: 0;
			flex-grow: 1;
			max-width: 100%;
			padding-right: 1rem; } } }
// ≥992px
@include media-breakpoint-down(md) {
	.results-content {
		height: calc(100vh - 12rem); }
	.result, .public {
		.footer {
			padding-left: 0!important; }
		.footer-geo {
			position: relative;
			right: auto;
			white-space: normal;
			.form-geo {
				padding-left: 0; } } }
	.results.is-view {
		.results-content {
			height: calc(100vh - 9rem); } }
	.catalog-category {
		&_list {
			column-count: 2; }
		&_item:nth-child(n+11) {
			display: none; } } }
// ≥768px
@include media-breakpoint-down(sm) {
	.result-v,
	.result-v body {
		overflow: hidden;
		position: relative;
		height: 100%; }
	.result-v {
		.results {
			width: 100%;
			top: calc(100vh - 7rem);
			transform: translateX(0);
			padding-top: 2rem;
			&:before {
				content: "";
				top: .9rem;
				position: absolute;
				@include size(3.2rem, .2rem);
				background: #dfdfdf;
				opacity: .4;
				left: 50%;
				transform: translateX(-50%); }
			&.is-open {
				top: 7rem;
				&:before {
					opacity: 1; } }
			&-content {
				height: calc(100vh - 9rem); }
			&-control {
				display: none; } } }
	.result, .index, .public {
		.header-bar {
			flex: 0 0 auto;
			width: auto;
			max-width: 100%;
			background-color: transparent;
			padding: {
				right: 1rem;
				left: 1rem; } } }
	.index {
		.aside {
			&-content {
				padding: 1.5rem 1rem 0; }
			&-catalog {
				height: calc(100vh - 14.5rem);
				margin: {
					top: 1.5rem;
					right: -1rem; }
				.jspPane > * {
					padding-right: 1rem; }
				&_text {
					margin-bottom: 1.5rem; }
				&_list {
					padding-bottom: 1.5rem; } } } }
	.header {
		z-index: 10;
		position: relative; }
	.result, .public {
		.header-bar {
			flex: 0 0 auto;
			width: auto;
			max-width: 100%; }
		.map {
			height: calc(100vh - 12rem); } }
	.filter {
		padding: {
			left: 1rem!important;
			right: 1rem!important; }
		> .row {
			margin: {
				left: -1rem;
				right: -1rem; }
			flex-wrap: nowrap;
			overflow-x: auto; }
		&-popup {
			display: none;
			position: relative;
			opacity: 1;
			visibility: visible;
			pointer-events: auto;
			width: 30rem;
			padding: 0;
			.btn-icon {
				display: none; } } }
	.results {
		top: 12rem;
		&-content {
			height: calc(100vh - 12rem); } }
	.login-content_inner {
		padding: 2rem 1rem; }
	.progress {
		margin-left: 0; }
	.catalog {
		&-block {
			&_aside {
				padding: 1.5rem 0 1.5rem 1rem; }
			&_btn {
				padding-right: 1rem;
				margin-bottom: 1.5rem; }
			&_details {
				padding: 1.5rem 1rem; } }
		&-sections {
			height: calc(100vh - 9rem);
			&_list {
				padding-right: 1rem; } }
		&-category {
			&_list {
				column-count: 1; }
			&_item:nth-child(n+5) {
				display: none; } }
		&-company {
			margin-bottom: 2rem; }
		&-category {
			height: calc(100vh - 14rem); } }
	.form-address {
		margin: {
			left: -1rem;
			right: -1rem; }
		> * {
			padding: {
				left: 1rem;
				right: 1rem; } } } }
// ≥576px
@include media-breakpoint-down(xs) {
	.map-add {
		left: 1.5rem;
		&_adv {
			right: 1.5rem;
			bottom: 7.5rem; } }
	.popup {
		min-width: 30rem; }
	.filter {
		.row {
			margin: {
				left: -1rem;
				right: -1rem; }
			> * {
				padding: {
					left: 1rem;
					right: 1rem; } } }
		.btn:not(.btn-primary) {
			padding: {
				left: 0;
				right: 0; }
			min-width: auto; } }
	.profile-pages {
		height: auto;
		flex: 0 0 100%;
		max-width: 100%;
		&_block {
			min-height: 5rem; } }
	.login-content {
		height: auto; }
	.result,
	.result body {
		overflow: hidden;
		position: relative;
		height: 100%; }
	.results {
		width: 100%;
		top: calc(100vh - 11rem);
		transform: translateX(0);
		padding-top: 2rem;
		&:before {
			content: "";
			top: .9rem;
			position: absolute;
			@include size(3.2rem, .2rem);
			background: #dfdfdf;
			opacity: .4;
			left: 50%;
			transform: translateX(-50%); }
		&.is-open {
			top: 12rem;
			&:before {
				opacity: 1; } }
		&-content {
			height: calc(100vh - 14rem); }
		&-control {
			display: none; } }
	.catalog {
		&-block {
			width: 100%;
			max-width: none;
			&_aside {
				flex: 0 0 100%;
				max-width: 100%; }
			&_btn .btn {
				justify-content: center; }
			&_details.is-show {
				flex: 0 0 100%;
				max-width: 100%;
				z-index: 1000;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%; } }
		&-category {
			height: calc(100vh - 17rem); } }
	.profile {
		&-pages + .col-auto {
			flex-basis: 100%;
			max-width: 100%; }
		&-pages_block {
			padding-left: 0rem;
			padding-right: 1rem; } }
	.login-content_inner {
		padding-left: 0;
		padding-right: 0; } }
// ≥410px
@include media-breakpoint-down(xxs) {
	.map-control {
		left: 1rem;
		top: 1rem;
		right: 1rem; }
	.index,
	.index body {
		overflow: hidden;
		position: relative;
		height: 100%; }
	.index {
		.aside {
			width: 100%;
			top: calc(100vh - 11rem);
			transform: translateX(0);
			&:before {
				content: "";
				top: .9rem;
				position: absolute;
				@include size(3.2rem, .2rem);
				background: #dfdfdf;
				opacity: .4;
				left: 50%;
				transform: translateX(-50%); }
			&-content {
				padding: 2rem 1rem 0; }
			&-catalog {
				height: calc(100vh - 15rem); }
			&-control {
				display: none; }
			&.is-open {
				top: 7rem;
				&:before {
					opacity: 1; } } } }
	.header-city {
		flex: 0 0 7rem;
		max-width: 7rem;
		&_link {
			width: 100%;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis; } }
	.results.is-view .results-content .article {
		padding: 0 1rem 2rem; }
	.breadcrumbs-list_main {
		display: none; }
	.owl-nav {
		width: calc(100% + 2rem); }
	.article {
		padding: {
			left: 1rem;
			right: 1rem; } }
	.form-days {
		width: 30rem;
		.form-label {
			width: 4.2rem; } }
	.form-address {
		margin: {
			left: -.5rem;
			right: -.5rem; }
		> * {
			padding: {
				left: .5rem;
				right: .5rem; } } }
	.form-phone .form-control {
		width: auto;
		max-width: 16.8rem; } }
/*==========  Mobile First  ==========*/
// ≤576px
@include media-breakpoint-up(xs);

// ≤576px
@include media-breakpoint-up(sm);

// ≤768px
@include media-breakpoint-up(md);

// ≤992px
@include media-breakpoint-up(lg);

// ≤1200px
@include media-breakpoint-up(xl);
